* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: "Reem Kufi", sans-serif !important;
}

img {
    max-width: 100%;
    height: auto;
}

.App {
    min-height: 100vh;
    width: 85vw;
    max-width: 1900px;
    margin: 0rem auto;
}

body {
    max-width: 100%;
    overflow-x: hidden;
}

body {
    background-color: wheat;
}

nav {
    display: flex;
    align-items: center;
    justify-content: space-between;
    min-height: 90px;
}

.navbar-menu-container {
    display: none;
}

.navbar-links-container a {
    margin-right: 3rem;
    text-decoration: none;
    color: rgb(99, 73, 5);
    font-size: 1.5rem;
    font-weight: 600;
    transition: font-size 0.2s ease-in-out 0.05s;
}

.navbar-links-container home_button {
    margin-right: 3rem;
    text-decoration: none;
    color: rgb(99, 73, 5);
    font-size: 1.5rem;
    font-weight: 600;
    transition: font-size 0.2s ease-in-out 0.05s;
}

.navbar-links-container a:hover {
    margin-right: 3rem;
    text-decoration: none;
    color: coral;
    font-size: 1.8rem;
    font-weight: 600;
    text-decoration: underline;
}

.primary-button {
    padding: 0.9rem 1.75rem;
    background-color: whitesmoke;
    color: black;
    outline: none;
    border: none;
    border-radius: 5rem;
    font-size: 1.1rem;
    cursor: pointer;
    font-weight: 600;
    transition: transform 0.3s ease 0.1s;
}

.navbar-cart-icon {
    font-size: 1.15rem;
    transition: font-size 0.2s ease-in-out 0.1s;
}

.navbar-cart-icon:hover {
    font-size: 2rem;
}

.primary-button:hover {
    background-color: coral;
    color: whitesmoke;
    transform: scale(1.3);
}

.navbar-menu-container svg {
    font-size: 1.5rem;
    cursor: pointer;
}

.home-banner-container {
    position: relative;
    display: flex;
    padding-top: 3rem;
}

.home-bannerImage-container {
    position: absolute;
    top: -90px;
    right: -170px;
    z-index: -2;
    max-width: 700px;
}

.home-image-section {
    max-width: 600px;
    flex: 1;
}

.home-text-section {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
}

.primary-heading {
    font-size: clamp(2rem, 5vw, 3.5rem);
    color: #4c4c4c;
    /* line-height: 5rem; */
    max-width: 600px;
    text-align: center;
}

.primary-text {
    font-size: clamp(1rem, 3vw, 1.5rem);
    max-width: 500px;
    color: #6a6a6a;
    margin: 1.5rem;
    text-align: center;
    margin-left: 50px;
}

.secondary-button {
    padding: 1rem 2.5rem;
    background-color: #fe9e0d;
    outline: none;
    border: none;
    border-radius: 5rem;
    font-size: 1.1rem;
    cursor: pointer;
    font-weight: 600;
    color: white;
    transition: 0.2s;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: transform 0.3s ease 0.1s;
}

.secondary-button svg {
    margin-left: 0.75rem;
    font-size: 1.5rem;
}

.secondary-button:hover {
    background-color: coral;
    transform: scale(1.3);
}

.secondary-button_about {
    padding: 1rem 2.5rem;
    background-color: #fe9e0d;
    outline: none;
    border: none;
    border-radius: 5rem;
    font-size: 1.1rem;
    cursor: pointer;
    font-weight: 600;
    color: white;
    transition: 0.2s;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: transform 0.3s ease 0.1s;
    text-decoration: none;
}

.secondary-button_about svg {
    margin-left: 0.75rem;
    font-size: 1.5rem;
    text-decoration: none;
}

.secondary-button_about:hover {
    background-color: coral;
    transform: scale(1.3);
}

.about-section-container {
    margin-top: 20rem;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.about-background-image-container {
    position: absolute;
    left: -150px;
    z-index: -2;
}

.about-section-image-container {
    flex: 1;
    margin-right: 10rem;
    margin-left: 12rem;
}

.about-section-text-container {
    flex: 1;
    justify-content: center;
    display: flex;
    flex-direction: column;
}

.primary-subheading {
    font-weight: 700;
    color: #fe9e0d;
    font-size: 3rem;
    text-decoration: underline;
    text-align: center;
}

.primary-subheading1 {
    font-weight: 700;
    color: #fe9e0d;
    font-size: 3rem;
    text-decoration: underline;
    text-align: center;
}

.primary-subheading2 {
    font-weight: 700;
    color: #fe9e0d;
    font-size: 3rem;
    text-decoration: underline;
    text-align: center;
}

.about-buttons-container {
    margin-top: 2rem;
    display: flex;
}

.watch-video-button {
    margin-left: 2rem;
    background-color: transparent;
    outline: none;
    border: none;
    border-radius: 5rem;
    font-size: 1.1rem;
    cursor: pointer;
    font-weight: 600;
    color: #484848;
    transition: 0.2s;
    display: flex;
    align-items: center;
    justify-content: center;
}

.watch-video-button svg {
    font-size: 3rem;
    margin-right: 1rem;
}

.contact-page-wrapper h1 {
    max-width: 900px !important;
}

.contact-page-wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    margin: 6rem 0rem;
}

.contact-form-container {
    background-color: white;
    max-width: 700px;
    width: 100%;
    margin-top: 3rem;
    display: flex;
    align-items: center;
    padding: 1rem;
    border-radius: 5rem;
}

.contact-form-container input {
    flex: 1;
    height: 100%;
    border: none;
    outline: none;
    font-size: 1.3rem;
    padding: 0.5rem 1rem;
}

.footer-wrapper {
    margin: 6rem 0rem;
    display: flex;
    margin-top: 10rem;
}

.footer-logo-container {
    max-width: 110px;
}

.footer-icons {
    margin-top: 2.5rem;
}

.footer-icons svg {
    font-size: 1.5rem;
    margin-right: 1.25rem;
    color: #585858;
}


/* .footer-section-one {
  border: 2px solid blue;
} */

.footer-section-two {
    flex: 1;
    display: flex;
    justify-content: flex-end;
}

.footer-section-columns {
    display: flex;
    flex-direction: column;
    min-width: 220px;
}

.footer-section-columns span {
    margin: 0.25rem 0rem;
    font-weight: 600;
    color: #4c4c4c;
    cursor: pointer;
}

.testimonial-section-bottom {
    margin-top: 2rem;
    background-color: white;
    padding: 1.5rem 2rem;
    display: flex;
    align-items: center;
    justify-content: center;
    max-width: 750px;
    border-radius: 2rem;
    flex-direction: column;
    text-align: center;
}

.testimonial-section-bottom {
    margin: 2rem auto;
}

.testimonial-section-bottom p {
    font-weight: 700;
    color: #515151;
    max-width: 500px;
    font-size: 1.1rem;
    margin: 2rem 0rem;
}

.testimonials-stars-container svg {
    margin: 0rem 0.25rem;
    font-size: 1.5rem;
    color: #fe9e0d;
}

.testimonial-section-bottom h2 {
    margin-top: 1.5rem;
}

.testimonial-section-bottom-img {
    border-radius: 50%;
    width: 200px;
    height: 200px;
}

.work-section-wrapper {
    margin-top: 15rem;
}

.work-section-top p {
    text-align: center;
    max-width: 600px !important;
}

.work-section-top h1 {
    max-width: 700px !important;
}

.work-section-top {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.work-section-info {
    width: 290px;
    min-height: 350px;
    background-color: white;
    padding: 1rem 2rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    border-radius: 1rem;
    color: #505050;
    margin: 1rem 2rem;
}

.work-section-info h2 {
    margin: 1rem 0rem;
}

.work-section-bottom {
    margin-top: 5rem;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
}

.work-section-info p {
    flex: 1;
    display: flex;
    align-items: center;
    font-weight: 600;
}

@media (max-width: 1000px) {
    .navbar-links-container a {
        margin-right: 1rem;
        font-size: 1rem;
    }
    .primary-button {
        font-size: 1rem;
    }
    .home-bannerImage-container {
        max-width: 600px;
    }
}

@media (max-width: 800px) {
    .nav-logo-container {
        max-width: 140px;
    }
    .navbar-links-container {
        display: none;
    }
    .navbar-menu-container {
        display: flex;
    }
    .home-bannerImage-container {
        max-width: 450px;
    }
    .home-banner-container,
    .about-section-container {
        flex-direction: column-reverse;
        align-items: center;
        justify-content: center;
    }
    .about-section-container {
        flex-direction: column;
    }
    .home-image-section,
    .primary-heading {
        text-align: center;
        max-width: 90%;
    }
    .primary-text {
        text-align: center;
        max-width: 80%;
    }
    .home-text-section,
    .about-section-text-container {
        justify-content: center;
        align-items: center;
        margin-top: 4rem;
    }
    .secondary-button {
        font-size: 1rem;
        padding: 0.8rem 2rem;
        cursor: pointer;
    }
    .about-section-container {
        margin-top: 5rem !important;
    }
    .about-buttons-container {
        justify-content: center;
        flex-direction: column;
    }
    .primary-subheading {
        text-align: center;
    }
    .watch-video-button {
        margin-left: 0rem !important;
        margin-top: 1rem;
        font-size: 1rem !important;
    }
    .watch-video-button svg {
        margin-right: 0.5rem !important;
    }
    .work-section-wrapper {
        margin-top: 5rem !important;
    }
    .work-section-bottom {
        margin-top: 1rem !important;
    }
    .contact-form-container {
        padding: 0.5rem !important;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        border-radius: 1rem !important;
    }
    .contact-form-container input {
        font-size: 1rem !important;
        max-width: 100%;
        padding: 0.5rem 1rem !important;
        margin-bottom: 0.8rem !important;
        text-align: center;
    }
    .footer-wrapper {
        flex-direction: column;
    }
    .footer-section-two {
        flex-direction: column;
        margin-top: 2rem;
    }
    .footer-section-columns {
        margin: 1rem 0rem;
    }
    .App {
        max-width: 95vw;
    }
}

@media (max-width: 600px) {
    .home-bannerImage-container,
    .about-background-image-container {
        display: none;
    }
}

.course_block {
    width: calc(25% - 15px);
    top: 100px;
    left: 90px;
    bottom: 100px;
    display: inline-block;
    vertical-align: top;
    margin: 50px 50px;
    box-shadow: 0 0 10px rgba(0, 0, 0, .3);
    color: rgba(0, 0, 0, .6);
    transition: .8s;
    text-decoration: none;
    position: relative;
    overflow: hidden;
    box-sizing: border-box;
}

.course_block:hover .course_image>img {
    filter: blur(4px);
    -webkit-filter: blur(4px);
}

.course_block:nth-child(3n),
.course_block:nth-child(3n+1) {
    margin: 50px 50px;
}

.course_block img {
    width: 100%;
    transition: .8s
}

.course_details {
    padding: 10px 15px;
}

.course_details p {
    max-height: 38px;
    overflow: hidden
}

.course_block h3 {
    font-size: 16px;
    margin: 0 0 10px 0;
    text-align: center;
}

.course_block p {
    font-size: 14px;
    margin: 0;
}

.course_image {
    position: relative;
    overflow: hidden;
}

.course_date {
    position: absolute;
    bottom: 18px;
    right: 12px;
    background: rgba(0, 0, 0, .6);
    font-size: 13px;
    padding: 4px 8px;
    border-radius: 3px;
    color: #fff;
}

.course_top_notices {
    position: absolute;
    top: 10px;
    right: 12px;
}

.course_new,
.course_unique {
    font-size: 12px;
    display: inline-block;
    padding: 2px 5px;
    font-weight: bold;
    border-radius: 3px;
    margin-left: 5px;
}

.course_unique,
.course_new {
    background: white;
}

.view_course {
    position: absolute;
    top: 40%;
    left: calc(50% - 70px);
    width: 140px;
    text-align: center;
    background: rgba(0, 0, 0, .1);
    box-sizing: border-box;
    padding: 8px 10px;
    border-radius: 5px;
    border: 1px solid #fff;
    color: #fff;
    transition: .8s;
    opacity: 0
}

.course_block:hover .view_course {
    opacity: 1
}